import {config as liveConfig} from './live/fastmesh';
// let liveConfig = require('./live/fastmesh').config || {};
import {config as vodConfig} from './vod/bittorrent';


//
let defaultP2PConfig = {
    key: 'free',                                //RPAPI key
    mode: 'vod',                               //livevodvod

    wsSignalerAddr: 'ws://120.78.168.126:8081/ws',          //
    wsMaxRetries: 3,                           //
    wsReconnectInterval: 5,                     //websocket

    p2pEnabled: true,                           //P2Ptrue

    dcKeepAliveInterval: 10,                    //datachannelkeep-alive
    dcKeepAliveAckTimeout: 2,                   //datachannelkeep-alive-ack
    dcRequestTimeout: 2,                        //datachannel

    packetSize: 16*1024,                        //datachannel
    maxBufSize: 1024*1024*50,                   //p2p
    loadTimeout: 5,                             //p2p
    reportInterval: 60,                         //

};

let p2pConfig;

if (__EXCLUDE_LIVE__) {
    p2pConfig = Object.assign(defaultP2PConfig, vodConfig);
} else if (__EXCLUDE_VOD__) {
    p2pConfig = Object.assign(defaultP2PConfig, liveConfig);
} else {
    p2pConfig = Object.assign(defaultP2PConfig, liveConfig, vodConfig);
}



export default p2pConfig;



